export type Product = {
  id: string;
  sku: string;
  name: string;
  unit: string;
  stock: number;
  minStock: number;
  image?: string; // ✅ base64 (data:image/...)
};

export type TxType = "IN" | "OUT";

export type StockTx = {
  id: string;
  type: TxType;
  productId: string;
  qty: number;
  at: number; // timestamp
};

const KEY_PRODUCTS = "stock_app_products_v2";
const KEY_TX = "stock_app_txs_v1";

export function loadProducts(): Product[] {
  if (typeof window === "undefined") return [];
  const raw = localStorage.getItem(KEY_PRODUCTS);
  if (!raw) return [];
  try {
    return JSON.parse(raw) as Product[];
  } catch {
    return [];
  }
}

export function saveProducts(products: Product[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_PRODUCTS, JSON.stringify(products));
}

export function loadTxs(): StockTx[] {
  if (typeof window === "undefined") return [];
  const raw = localStorage.getItem(KEY_TX);
  if (!raw) return [];
  try {
    return JSON.parse(raw) as StockTx[];
  } catch {
    return [];
  }
}

export function saveTxs(txs: StockTx[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_TX, JSON.stringify(txs));
}

export function seedProductsIfEmpty() {
  const current = loadProducts();
  if (current.length > 0) return;

  const seeded: Product[] = [
    {
      id: crypto.randomUUID(),
      sku: "P001",
      name: "น้ำปลา",
      unit: "ขวด",
      stock: 24,
      minStock: 10,
    },
    {
      id: crypto.randomUUID(),
      sku: "P002",
      name: "ซีอิ๊วขาว",
      unit: "ขวด",
      stock: 2,
      minStock: 8,
    },
    {
      id: crypto.randomUUID(),
      sku: "P003",
      name: "น้ำมันพืช",
      unit: "ขวด",
      stock: 4,
      minStock: 6,
    },
  ];

  saveProducts(seeded);
}

export function addProduct(p: Omit<Product, "id" | "stock">) {
  const products = loadProducts();
  const next: Product = { id: crypto.randomUUID(), stock: 0, ...p };
  saveProducts([next, ...products]);
}

export function deleteProduct(id: string) {
  const products = loadProducts();
  saveProducts(products.filter((p) => p.id !== id));

  // ลบประวัติที่เกี่ยวข้องด้วย (ไม่บังคับ แต่สะอาด)
  const txs = loadTxs();
  saveTxs(txs.filter((t) => t.productId !== id));
}

export function adjustStockWithTx(productId: string, type: TxType, qty: number) {
  const delta = type === "IN" ? qty : -qty;

  const products = loadProducts();
  const nextProducts = products.map((p) => {
    if (p.id !== productId) return p;
    return { ...p, stock: Math.max(0, p.stock + delta) };
  });
  saveProducts(nextProducts);

  const txs = loadTxs();
  const tx: StockTx = {
    id: crypto.randomUUID(),
    type,
    productId,
    qty,
    at: Date.now(),
  };
  saveTxs([tx, ...txs]);
}

export function getDashboardSummary() {
  const products = loadProducts();
  const txs = loadTxs();

  const totalProducts = products.length;
  const lowStockCount = products.filter((p) => p.stock <= p.minStock).length;

  // วันนี้ (ตามเครื่อง)
  const now = new Date();
  const start = new Date(now.getFullYear(), now.getMonth(), now.getDate()).getTime();
  const end = start + 24 * 60 * 60 * 1000;

  const todayTxs = txs.filter((t) => t.at >= start && t.at < end);
  const inToday = todayTxs
    .filter((t) => t.type === "IN")
    .reduce((sum, t) => sum + t.qty, 0);
  const outToday = todayTxs
    .filter((t) => t.type === "OUT")
    .reduce((sum, t) => sum + t.qty, 0);

  const lowStockProducts = products
    .filter((p) => p.stock <= p.minStock)
    .sort((a, b) => a.stock - b.stock)
    .slice(0, 5);

  const latestTxs = txs.slice(0, 5);

  return {
    totalProducts,
    lowStockCount,
    inToday,
    outToday,
    lowStockProducts,
    latestTxs,
    products,
  };
}

export function formatTxLabel(txType: TxType) {
  return txType === "IN" ? "รับเข้า" : "เบิกออก";
}
